<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="addStudent">添加学生</el-button>
		</div>
		<div class="btn">
			<el-input v-model="number" placeholder="输入学生学号" class="search" @keyup.enter.native="search(number)"></el-input>
		</div>
		<div class="btn">
			<el-button type="primary" @click="search(number)">搜索</el-button>
		</div>
		<div class="btn">
			<el-button type="success" @click="refresh">刷新页面</el-button>
		</div>

		<el-dialog title="添加学生" :visible.sync="dialogStudent">
			<div class="box">
				<el-form :model="form" label-width="100px">
					<el-form-item label="年级">
						<el-select v-model="grade" placeholder="请选择年级" @change="gradeOnChange">
							<el-option v-for="item in gradeList" :key="item.id" :label="item.title" :value="item.id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="班级">
						<el-select v-model="classValue" placeholder="请选择班级" @change="classOnChange">
							<el-option v-for="item in classList" :key="item.id" :label="item.title" :value="item.id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="姓名">
						<el-input v-model="form.name"></el-input>
					</el-form-item>
					<el-form-item label="学号">
						<el-input v-model="form.number"></el-input>
					</el-form-item>
					<el-form-item label="性别">
						<el-radio-group v-model="form.sex">
							<el-radio :label='1'>男</el-radio>
							<el-radio :label='2'>女</el-radio>
						</el-radio-group>
					</el-form-item>
					<el-form-item label="年龄">
						<el-input v-model="form.age"></el-input>
					</el-form-item>
					<el-form-item label="只允许进校">
						<el-radio-group v-model="form.only_in">
							<el-radio :label='1'>是</el-radio>
							<el-radio :label='2'>否</el-radio>
						</el-radio-group>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="newStudent">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>

		<el-table :data="tableDate" ref="multipleTable" class="aip-table">
			<el-table-column prop="id" label="学生ID" align="center"></el-table-column>
			<el-table-column prop="number" label="学号" align="center"></el-table-column>
			<el-table-column prop="name" label="姓名" align="center"></el-table-column>
			<el-table-column prop="face_image" label="人脸信息" align="center">
				<template slot-scope="scope"><img :src="scope.row.face_image" style="max-width:180px;max-height:80px;" /></template>
			</el-table-column>
			<el-table-column prop="sex" label="性别" align="center" width="100px">
				<template slot-scope="scope"><span v-text="scope.row.sex == '1' ? '男' : '女'"></span></template>
			</el-table-column>
			<el-table-column prop="age" label="年龄(岁)" align="center" width="100px"></el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center" width="400px">
				<template slot-scope="scope">
					<el-button size="mini" type="primary" @click="handleFace(scope.$index, scope.row)">更换人脸</el-button>
					<el-button size="mini" type="primary" @click="handleFamily(scope.$index, scope.row)">绑定家长</el-button>
					<el-button size="mini" type="success" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button v-if="hideDel" type="danger" size="mini" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>

		<!-- 绑定家长 -->
		<el-dialog title="绑定家长" :visible.sync="dialogFamily" :close-on-click-modal="false" width="500px">
			<div class="box">
				<el-form :model="familyForm">
					<el-form-item label="选择家长">
						<el-select v-model="familyForm.user_id" filterable placeholder="请选择家长" @change="familyChange">
							<el-option v-for="item in familyList" :key="item.id" :label="item.id + ' ' + item.nickname" :value="item.id">
							</el-option>
						</el-select>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="bindFamily">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>


		<el-dialog title="更换人脸" :visible.sync="dialogFace" :close-on-click-modal="false">
			<div class="box">
				<el-form :model="bindFormFamily">
					<el-form-item label="选择家长">
						<el-select v-model="familyForm.user_id" filterable placeholder="请选择家长" @change="familyChange">
							<el-option v-for="item in familyList" :key="item.id" :label="item.id + ' ' + item.nickname" :value="item.id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="更换人脸图片">
						<el-upload action="https://upload-z2.qiniup.com" ref="upload" :limit="1" :before-upload="beforeAvatarUpload"
						 :on-success="handleAvatarSuccess" :on-remove="handleRemove" :on-exceed="handleExceed" :data="imgData">
							<el-button size="small" type="primary">选择图片</el-button>
						</el-upload>
						<div class="up-img">
							<img class="pic-box" :src="familyForm.href" v-if="familyForm.href">
						</div>
					</el-form-item>
				</el-form>
			</div>
		</el-dialog>

		<!-- 删除提示框 -->
		<el-dialog :visible.sync="dialogDel" title="删除年级" width="20%" align="center" :close-on-click-modal="false">
			<div style="font-size: 20px; margin-bottom: 30px;">是否删除该学生</div>
			<span>
				<el-button type="primary" @click="toDel">删除</el-button>
				<el-button type="danger" @click="dialogDel = false">取消</el-button>
			</span>
		</el-dialog>


	</div>
</template>

<script>
	import API from '@/api/index.js'
	import md5 from 'blueimp-md5'
	import axios from 'axios'

	let href;
	export default {
		name: 'studentlist',
		data() {
			return {
				// 操作
				dialogStudent: false, // 添加编辑学生
				grade: '',
				gradeList: [],
				classValue: '',
				classList: [],
				form: {
					name: '',
					number: '',
					sex: 1,
					age: '',
					class_id: '',
					only_in: 1,
					grade_id: ''
				},
				dialogFace: false, // 查看人脸信息
				dialogDel: false, // 删除时确认
				id: '', // 删除的学生id
				dialogFamily: false, // 家长信息
				familyList: [],
				// nickname: '',
				disabled: false,
				imgData: {
					key: '',
					token: ''
				},
				fileName: '',
				suffix: '',
				qiniuaddr: "https://tu.fengniaotuangou.cn", // 七牛云图片外链地址
				familyForm: {
					href: '',
					user_id: '',
					student_id: ''
				},
				number: '', // 搜索
				bindFormFamily: {
					user_id: ''
				},

				tableDate: [],
				face_image: '',
				// 分页
				currentPage: 1,
				totalPage: 0,

				permissions: localStorage.getItem('permissions'),
				role: localStorage.getItem('role'),
				hideDel: false // 是否可以使用删除按钮
			}
		},
		mounted() {
			this.getStudent();
			this.getGrade();
			// this.getFaceInfo();
			// this.getUptoken();
			this.getQiniuToken();
		},
		methods: {
			familyChange(val) {
				console.log('familyChange', val)
				console.log('familyChange', this.familyForm.user_id)
			},
			// 获取年级
			getGrade() {
				var self = this;
				API.grades(self.currentPage).then(res => {
					self.gradeList = res.data;
				})
			},
			// 获取学生列表
			refresh() {
				var self = this;
				API.students(1).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
					self.currentPage = 1;
				})
			},
			getStudent() {
				var self = this;
				if (self.permissions.indexOf('infomationGet') > -1 || self.role === 'super') {
					if (self.permissions.indexOf('infomationDel') > -1 || self.role === 'super') {
						self.hideDel = true;
					}
					API.students(self.currentPage).then(res => {
						self.tableDate = res.data;
						self.totalPage = res.total;
					})
				} else {
					self.$message.warning('无此权限')
				}
			},
			getClass(grade_id) {
				var self = this;
				API.classes(1, grade_id, self.totalPage).then(res => {
					self.classList = res.data;
				})
			},
			// 年级选中
			gradeOnChange(value) {
				var self = this;
				self.grade = value;
				self.getClass(value)
				self.form.grade_id = value;
			},
			// 班级选中
			classOnChange(value) {
				var self = this;
				self.form.class_id = value;
			},
			addStudent() {
				var self = this
				if (self.permissions.indexOf('infomationAdd') > -1 || self.role === 'super') {
					self.dialogStudent = true;
					self.grade = '';
					self.classValue = '';
					self.form = {
						name: '',
						number: '',
						sex: 1,
						age: '',
						class_id: '',
						only_in: 1
					};
				} else {
					self.$message.warning('无此权限')
				}
			},
			newStudent() {
				var self = this;
				API.student(self.form).then(res => {
					self.$message.success("提交成功");
					self.getStudent();
					self.dialogStudent = false;
					self.grade = '';
					self.classValue = '';
					self.form = {
						name: '',
						number: '',
						sex: 1,
						age: '',
						class_id: '',
						only_in: 1
					};
					self.currentPage = 1;
				})
			},
			// 搜索
			search() {
				var self = this;
				if (self.number) {
					API.search(self.number).then(res => {
						self.tableDate = res.data;
						self.totalPage = 1,
							self.number = '',
							self.$message.success('搜索成功！');
					})
				} else {
					self.$message.warning('请输入学生学号');
				}

			},
			// 操作
			// 绑定家长
			bindFamily() {
				var self = this;
				
			},
			handleFace(index, row) {
				var self = this
				if (self.permissions.indexOf('infomationEdit') > -1 || self.role === 'super') {
					self.dialogFace = true;
					self.familyForm.student_id = row.id;
					API.bindFamily(self.currentPage, 10000).then(res => {
						self.familyList = res.data
					})
				} else {
					self.$message.warning('无此权限');
				}
			},
			// 编辑学生
			handleEdit(index, row) {
				var self = this;
				if (self.permissions.indexOf('infomationEdit') > -1 || self.role === 'super') {
					self.dialogStudent = true;
					row.sex = Number(row.sex);
					self.form = row;
					self.grade = row.grade_id;
					self.classValue = row.class;
				} else {
					self.$message.warning('无此权限');
				}

			},
			handleDel(index, row) {
				var self = this
				self.dialogDel = true;
				self.id = row.id;
			},
			toDel() {
				var self = this;
				API.delStudent(self.id).then(res => {
					self.$message.success('删除成功');
					self.dialogDel = false;
					self.getStudent();
					self.currentPage = 1;
				})
			},
			// 绑定家长
			handleFamily() {
				var self = this;
				self.dialogFamily = true;
				API.bindFamily(self.currentPage, 10000).then(res => {
					self.familyList = res.data
				})
			},

			// 人脸信息
			handleRemove(file, fileList) {},
			beforeAvatarUpload(file) {
				var self = this;
				self.fileName = md5(file.name);
				self.suffix = file.name.substring(file.name.lastIndexOf('.') + 1);
				self.imgData.key = `tmp_${self.fileName}.${self.suffix}`
			},
			handleAvatarSuccess(res, file) {
				var self = this;
				// self.imageUrl = URL.createObjectURL(file.raw);
				file.url = `${self.qiniuaddr}/${res.key}`;
				self.familyForm.href = file.url;
				API.updateFace(self.familyForm).then(res => {
					self.$message.success('上传成功');
					self.currentPage = 1;
					self.getStudent();
					self.$refs.upload.clearFiles()
					self.familyForm.href = '';
					self.imgData.key = '';
					self.familyForm.user_id = '';
					self.dialogFace = false;
				})
			},
			handleExceed(file, fileList) { //图片上传超过数量限制
				var self = this;
				self.$message.error('上传图片不能超过1张!重新上传');
				self.$refs.upload.clearFiles()
				self.familyForm.href = '';
				self.imgData.key = '';
				self.familyForm.user_id = '';
			},

			getQiniuToken() {
				var self = this;
				axios.get('https://api.fengniaotuangou.cn/api/upload/token').then(res => {
					self.imgData.token = res.data.uptoken;
				})
			},


			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getStudent();
			}
		}
	}
</script>

<style scoped>
	.upload-btn {
		margin-top: 10px;
	}

	.up-img {
		margin: 10px 50px;
	}

	.pic-box {
		max-width: 100%;
		height: 200px;
	}
</style>
